.App {
    text-align: center;
  }
  
  .App-logo {
    height: 40vmin;
    pointer-events: none;
  }
  
  @media (prefers-reduced-motion: no-preference) {
    .App-logo {
      animation: App-logo-spin infinite 20s linear;
    }
  }
  
  .App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
  }
  
  .App-link {
    color: #61dafb;
  }
  
  @keyframes App-logo-spin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }
  
  .profile-picture {
    width: 24%;
    height: 11%
  }
  .aboutMe {
  display: inline;
  line-height: 35px;
  margin-left: 130px;
  flex-wrap: wrap;
  }
  
  .contactMargin {
    margin: "400px";
    padding: 50px;
  }
  
  .aboutMeContainer {
    display: flex;
    align-items: center;
    justify-content: center
  }
  
  img {
    
    max-width: 100%
  }
  
  .profile-pic {
    flex-basis: 60%;
    margin: 26px;
  }
  
  .text {
    font-size: 20px;
    padding-left: 20px;
  }
  